

import React from 'react'
import "./post.css";
import {Posts} from "../../projectData";
import {useRef,useEffect} from "react"
import ReactPlayer from "react-player"
import Description from '../description/Description';

export default function Post() {


    const FadeIn = (duration = 0, delay = 0) => {
        const element = useRef();
        useEffect(() => {
            if (element.current) {
            const { current } = element;
            current.style.transition = `opacity ${duration}s ${delay}s`;
            current.style.opacity = 1;
            }
        }, []);
        return { ref: element, style: { opacity: 0 } };
    };

    const fadeInBox = FadeIn(3, 0.5);
    const fadeInDes = FadeIn(3,1);
    const fadeInVideo = FadeIn(3,1.5);

    return (
        <div className="postContainer">
                {Posts.map((p) => (
                    <Description
                        key={p.id}
                        post={p}
                    />
                ))}
                <div className="postVideo" {...fadeInBox}>
                    <div className="videoWrapper">
                        <p className="projectInfo" {...fadeInDes}>Unreal escape game. The user can interact with the doors, drawers, keypad on the door to insert a code. The user also can turn on/off flash and run until the stamina bar is empty</p>
                        <ReactPlayer
                            controls
                            width={'80%'}
                            height='420px'
                            url="https://www.youtube.com/watch?v=CWH1fMCkLtQ"
                        />
                    </div>
                </div>
        </div>
    )
}
