
import React from 'react';
import "./register.css"
import { useRef,useContext } from 'react';
import { AuthContext } from '../../context/AuthContext';
import { loginCall } from '../../apiCalls';
import axios from 'axios';
import { useHistory } from 'react-router';

const Register = () => {

    const username = useRef();
    const password = useRef();
    const history = useHistory();

    const {user,isFetching,error,dispatch} = useContext(AuthContext);


    const handleClick = async (e) => {
        e.preventDefault()
        try{
            const res = await axios.get(`/user?username=${username.current.value}`)
            loginCall({username:username.current.value,password:password.current.value},dispatch);
            history.push(`/rate/${res.username}`);
        } catch(err) {

        }
        try{
            const user = {
                username:username.current.value,
                password:password.current.value
            }

            await axios.post("/auth/register",user)
            loginCall({username:username.current.value,password:password.current.value},dispatch);
            history.push(`/rate/${user.username}`);
        } catch(err){

        }
    }

    return (
        <div className="registerContainer">
            <div className="registerWrapper">
                <p className="desc"> I am a highly motivated and willing to leran new skills. Please enter your name and set the password to leave a comment</p>
                <form className="registerBox" onSubmit={handleClick}>
                    <input placeholder="Name" required className="infoInput" ref={username}/>
                    <input placeholder="password" type="password" required maxLengt="10" className="infoInput" ref={password}/>
                    <button className="loginButton" type="submit">
                        Leave a comment
                    </button>
                </form>
            </div>
        </div>
    );
}

export default Register;
