import React from 'react';
import "./evaluate.css";
import ThumbUpOffAltIcon from "@mui/icons-material/ThumbUpOffAlt";
import ThumbDownOffAltIcon from '@mui/icons-material/ThumbDownOffAlt';
import { useState,useEffect,useContext } from 'react';
import axios from "axios"
import { LinkedCameraRounded } from '@material-ui/icons';
import { AuthContext } from "../../context/AuthContext";

const Evaluate = ({post}) => {

    const [like,setLike] = useState()
    const [isLiked, setIsLiked] = useState(false)
    const {user:currentUser} = useContext(AuthContext)

    useEffect(()=> {
        setIsLiked(post.likes.includes(currentUser._id))
    },[currentUser._id,post.likes]);

    const likeHandler = () => {
        try{
            axios.put("/posts/" + post._id + "/like",
            {userId:currentUser._id})
        } catch(err) {

        }
        // const sumLikes = res.
        setLike(isLiked ? like-1 : like+1)
        setIsLiked(!isLiked)
    }

    return (
        <div className="evaluateContainer">
            <div className="evaluateTop">
                <p className="desc">Thank you for reviewing my blog. Please rate me and let me know how I can improve</p>
                <div className="thumbs">
                    <div className="thumb">
                        <ThumbUpOffAltIcon className="thumbIcon" onClick={likeHandler}/>
                        <span className="likeCounter">{like}</span>
                    </div>
                    <div className="thumb">
                        <ThumbDownOffAltIcon className="thumbIcon" onClick={likeHandler}/>
                        <span className="dislikeCounter">{like}</span>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Evaluate;
